/*NAVBAR STYLE*/
.main-nav {
    top:0;
    position:fixed;
    justify-content: space-around;
    z-index: 100;
}
.navbar-nav {
    display: -ms-flexbox;
    display: flex;
    justify-content: space-around;
    padding-left: 0;
    margin-bottom: 0;
    list-style: none;
}
@media (min-width: 768px){
    .navbar-expand-md .navbar-collapse {
        display: -ms-flexbox!important;
        display: flex!important;
        -ms-flex-preferred-size: auto;
        flex-basis: auto;
        justify-content: space-between;
    }
}
@media (max-width: 768px){
    .navbar-nav{
        display: -ms-flexbox!important;
        display: flex!important;
        -ms-flex-preferred-size: auto;
        flex-basis: auto;
        flex-direction: row;
        justify-content: space-between;
    }
}
.navbar-dark {
    background-color: rgb(4,81,7);
}
.esparcir {
    right: 0px;
}
.imagen-navbar-profile{
    width: 30px;
    height: 30px;
    border-radius: 50%;
    max-width: auto;
    min-width: auto;
    object-fit: cover;
}

.notification {
    background-color: rgb(91, 179, 94);
    position: absolute;
    top: 38px;
    right: 15px;
    width: 375px;
    height: 362px;
    border: 1px solid black;
    border-top: none;
    overflow-x: hidden;
    overflow-y: scroll;
    scroll-behavior: smooth;
}

.pik{
    position: absolute;
    transform: rotate(45deg);
    background-color: rgb(91, 179, 94);
    border-top: 1px solid black;
    border-left: 1px solid black;
    width: 15px;
    height: 15px;
}
.pick-notification {
    top: 53px;
    right: 90px;
}

@media(max-width:767px){
    .notification {
        background-color: rgb(91, 179, 94);
        position: absolute;
        top: 119px;
        right: 15px;
        width: 375px;
        height: 362px;
        border: 1px solid black;
        border-top: none;
        overflow-x: hidden;
        overflow-y: scroll;
        scroll-behavior: smooth;
    }
    .pick-notification {
        top: 134px;
        right: 90px;
    }
}
@media (max-width: 409px) {
    .notification{
        width: 100%;
        right: 0;
    }
}
