/*USERPAGE*/
/*wall style*/
.user-info > *{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 5vh auto;
}
.user-info-description > * {
    font-size: 18px;
}
.user-info-post Button{
    background-color: #4CAF50;
    border: none;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    cursor: pointer;
}
.c span {
    display: flex;
    align-items: center;
}
.trashIcon {
    display: flex;
    position: absolute;
    display: none;
    justify-items: center;
}
div.c:hover  .trashIcon {
    display: block;
    position: absolute;
}
.wall-container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    box-sizing: border-box;
    margin: auto 5%;
}
.image-content {
    width: 33%;
    height: 26vw;
}  
.img-grid{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
/*IMAGE COMPONENT*/
.image-view{
    height: 100vh;
}
::-webkit-scrollbar {
    width: 10px;
}
/* Track */
::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 10px;
}
/* Handle */
::-webkit-scrollbar-thumb {
    background: #238626; 
    border-radius: 10px;
}
  
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #045107; 
}
