    color: green;
    border-top-style: hidden;
    border-right-style: hidden;
    border-left-style: hidden;
    border-bottom-style: hidden;
}
.sidenav {
    position: fixed;
    z-index: 1;
    font-size: 14px;
    top: 160px;
    right: 20px;
    overflow-x: hidden;
    padding-top: 20px;
    height: 60vh;
}
.scroll-aside {
    height: 50vh;
