/*INBOX COMPONENT*/
.inbox {
   color: rgb(0, 0, 0);
   height: 100%;
   text-overflow: ellipsis;
}
.inboxes {
    height: 100%;
    display: block;
    overflow-y: scroll;
    scroll-behavior: smooth;
    background: #b3b3b3;
    max-height: 73vh;
}
.texto {
    font-size: 1.3em
}
.message-display{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
}
.chat-field{
    display:flex;
    align-items: stretch;
    justify-content: stretch;
}
.chat-input{
    width: 80%;
}
.button-chat {
    background-color: #4CAF50;
    border: none;
    padding: 15px 32px;
    margin-right: 0px;
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    cursor: pointer;
    width: 20%;
}
.grid-inbox{
    width: 100%;
    height: 80vh;
    max-height: 80vh;
    display: grid;
    grid-template-columns: minmax(0, 1fr) minmax(0, 2fr) minmax(0, 1fr);
    grid-template-rows: 1fr;
    grid-gap: 10px;
}
.form-group.in-inbox{
margin-bottom: 0px;
}
@media(max-width: 800px){
    .grid-inbox{
        height: 80vh;
        display: grid;
        grid-template-columns: minmax(0, 2fr) minmax(0, 3fr);
        grid-template-rows: 2fr 1fr 2fr;
        grid-gap: 10px;
        grid-template-areas: 
            "one two"
            "three two"
            "three two";
    }
    .grid-inbox-one {
        grid-area: one;
        height: 80%;
    }
    .grid-inbox-two {
        grid-area: two;
        height: 100%;
    }
    .grid-inbox-three {
        grid-area: three;
        height: 80%;
    }
}
@media(max-width: 570px){
    .hide-name{
        display: none;
    }
}

/*chats colors*/
.chats{
    background-color: #59dc77;
    color: black;
    animation-name: gonzalo;
    animation-duration: 2s;
    animation-iteration-count: 5;
    animation-timing-function: linear;
    
}
.cursor{
    cursor: pointer;
}
@keyframes gonzalo {
    0%{
        background-color: #59dc77;
    }
    50%{
        background-color: #8ffaa8;
    }
    100%{
        background-color: #59dc77;
    }
}
